<template>
  <div class="limited-activity-wrapper">
    <van-cell-group :border="false">
      <van-cell class="title">
        <template #title>
          <div class="title-left"
               @click="clickTitleLeft">
            <span class="title-text-1">京东秒杀</span>
            <span class="title-text-2">10</span>
            <van-count-down :time="time">
              <template #default="{hours, minutes, seconds }">
                <div class="count-down">
                  <div class="time-number">{{hours|completionTime}}</div>
                  <span class="time-colon">:</span>
                  <div class="time-number">{{minutes|completionTime}}</div>
                  <span class="time-colon">:</span>
                  <div class="time-number">{{seconds|completionTime}}</div>
                </div>
              </template>
            </van-count-down>
          </div>
        </template>
        <template #right-icon>
          <div class="right-icon"
               @click="clickTitleRight">
            <div class="right-icon-text">更多秒杀</div>
            <van-icon class-prefix="icon"
                      name="limited-time-activity-title-right-icon"
                      class="right-icon-arrow" />
          </div>
        </template>
      </van-cell>
    </van-cell-group>

    <div class="goods-list">
      <div class="goods-item"
           @click="clickGoodsItem"
           v-for="item in goodsList"
           :key="item.id">
        <div class="goods-img">
          <img :src="item.url"
               alt="">
        </div>
        <div class="goods-completion">
          <div class="goods-new-completion">{{item.newCompletion|RMB}}</div>
          <div class="goods-old-completion">{{item.oldCompletion|RMB}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    goodsList: {
      type: Array,
      require: true,
    },
  },
  data() {
    return {
      time: 60 * 60 * 24 * 1000,
    }
  },
  filters: {
    completionTime(value) {
      return String(value).length > 1 ? value : '0' + value
    },
    RMB(value) {
      return '￥' + value
    },
  },
  methods: {
    clickTitleLeft() {
      this.ste()
    },
    clickTitleRight() {
      this.ste()
    },
    clickGoodsItem() {
      this.ste()
    },
  },
}
</script>

<style scoped>
.limited-activity-wrapper {
  margin: 0px 10px;
  margin-top: 10px;
  background-color: #fff;
  border-radius: 15px;
  overflow: hidden;
}

.title {
  background: url('data:image/png;base64,')
    no-repeat;
  background-size: contain;
  padding: 10px 5px;
}

.title-left {
  display: flex;
  align-items: center;
}

.title-text-1 {
  margin-right: 10px;
}

.title-text-2 {
  color: #fc3f2d;
}

.count-down {
  display: flex;
  height: 100%;
  margin-left: 20px;
}

.time-number {
  background: #fc3f2d;
  color: #fff;
  border-radius: 5px;
  padding: 0px 2px;
  font-size: 12px;
}

.time-colon {
  color: #fc3f2d;
  margin: 0px 2px;
  font-weight: 700;
}

.right-icon {
  font-size: 12px;
  color: #fc3f2d;
  display: flex;
  align-items: center;
  height: 24px;
}

.right-icon-arrow {
  width: 16px;
  height: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 5px;
}

.goods-list {
  display: flex;
  justify-content: space-between;
}

.goods-img {
  width: 70px;
  height: 80px;
}

.goods-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.goods-completion {
  margin-top: 5px;
  display: flex;
  height: 35px;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  font-size: 12px;
  line-height: 14px;
}

.goods-new-completion {
  color: #f2270c;
}

.goods-old-completion {
  color: #999;
  text-decoration-line: line-through;
}
</style>